/**
* Customized scrollbars
*/
.background {
  background-image: url(https://slideswith.com/cdn-cgi/image/w=1900,h=1400,fit=contain,metadata=none,onerror=false/https://back.slideswith.com/storage/o/public/deck-1205/1609894215521-8bit-background.png?token=c34f92a8-82f0-41fe-a237-ffff8d372ed2);
  background-size: 100%;
}  
 
/* to get pixelated images (nearest-neighbor filter) on all  browsers */
.rpgui-pixelated {
  image-rendering: -webkit-crisp-edges;
  image-rendering: -moz-crisp-edges;
  image-rendering: -o-crisp-edges;
  image-rendering: pixelated; }

/* unselectable text */
.rpgui-noselect {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  user-select: none; }

/* center things */
.rpgui-center {
  text-align: center;
  align-content: center; }

/* rotate object 90 degrees */
.rpgui-rotate-90 {
  /* rotate 90 degrees */
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
  /* rotate from center-left side */
  /* IE 9 */
  -webkit-transform-origin: 0% 50%;
  /* Chrome, Safari, Opera */
  transform-origin: 0% 50%; }

/**
* Styling for buttons
*/
/* button style */
.rpgui-button {
  /* hide button default stuff */
  background-color: Transparent;
  background-repeat: no-repeat;
  border: none;
  overflow: hidden;
  outline: none;
  /* background */
  background: url(/static/media/button.ab516115.png) no-repeat no-repeat;
  background-clip: padding-box;
  background-origin: padding-box;
  background-position: center;
  background-size: 100% 100%;
  /* font size */
  font-size: 1.0em;
  /* default size and display */
  max-width: 100%;
  min-width: 140px;
  height: 60px;
  display: inline-block;
  /* padding */
  padding-left: 35px;
  padding-right: 35px; }

/* button hover */
.rpgui-button.hover,
.rpgui-button:hover {
  background-image: url(/static/media/button-hover.d6f5e532.png); }

/* button clicked */
.rpgui-button.down,
.rpgui-button:active {
  background-image: url(/static/media/button-down.161363a7.png); }

/* golden button stuff */
.rpgui-button.golden p {
  display: inline-block; }

/* golden button style */
.rpgui-button.golden {
  /* hide button default stuff */
  background-color: Transparent;
  background-repeat: no-repeat;
  border: none;
  overflow: hidden;
  outline: none;
  /* background */
  background: url(/static/media/button-golden.322d6afa.png) no-repeat no-repeat;
  background-clip: padding-box;
  background-origin: padding-box;
  background-position: center;
  background-size: 100% 80%;
  /* default size and display */
  max-width: 100%;
  min-width: 140px;
  height: 60px;
  display: inline-block;
  /* padding */
  padding-top: 5px;
  padding-left: 35px;
  padding-right: 35px;
  overflow: visible; }

/* button hover */
.rpgui-button.golden.hover,
.rpgui-button.golden:hover {
  background-image: url(/static/media/button-golden-hover.a6d554ee.png); }

/* button clicked */
.rpgui-button.golden.down,
.rpgui-button.golden:active {
  background-image: url(/static/media/button-golden-down.775f4c04.png); }

.rpgui-button.golden:before {
  white-space: nowrap;
  display: inline-block;
  content: "";
  width: 34px;
  display: block;
  height: 110%;
  background: transparent url(/static/media/button-golden-left.818e3ec7.png) no-repeat right center;
  background-size: 100% 100%;
  margin: 0 0 0 0;
  left: 0px;
  float: left;
  margin-left: -46px;
  margin-top: -5%; }

.rpgui-button.golden:after {
  white-space: nowrap;
  display: block;
  content: "";
  width: 34px;
  height: 110%;
  background: transparent url(/static/media/button-golden-right.23bc080f.png) no-repeat left center;
  background-size: 100% 100%;
  margin: 0 0 0 0;
  right: 0px;
  float: right;
  margin-right: -46px;
  margin-top: -5%; }

