/**
* different cursor graphics
*/
/* default cursor important */
/* this rule is for when you specifically request this cursor class */
.rpgui-cursor-default {
  cursor: url(/static/media/default.ca598161.png), auto !important; }

/* default cursor, not important, for all elements without any other rule. */
.rpgui-content,
label {
  cursor: url(/static/media/default.ca598161.png), auto; }

/* pointer / hand cursor important */
/* this rule is for when you specifically request this cursor class */
.rpgui-cursor-point,
.rpgui-cursor-point * {
  cursor: url(/static/media/point.da411450.png) 10 0, auto !important; }

/* pointer / hand cursor, not important, for all elements that have pointer by-default */
.rpgui-content a,
.rpgui-content button,
.rpgui-button,
.rpgui-slider-container,
.rpgui-content input[type=radio].rpgui-radio + label,
.rpgui-list-imp,
.rpgui-dropdown-imp,
.rpgui-content input[type=checkbox].rpgui-checkbox + label {
  cursor: url(/static/media/point.da411450.png) 10 0, auto; }

/* for input / text selection important */
/* this rule is for when you specifically request this cursor class */
.rpgui-cursor-select,
.rpgui-cursor-select * {
  cursor: url(/static/media/select.f8dd50b4.png) 10 0, auto !important; }

/* for input / text selection, not important, for all elements that have pointer by-default */
.rpgui-cursor-select,
.rpgui-content input,
.rpgui-content textarea {
  cursor: url(/static/media/select.f8dd50b4.png) 10 0, auto; }

/* for grabbing stuff */
/* this rule is for when you specifically request this cursor class */
.rpgui-cursor-grab-open,
.rpgui-cursor-grab-open * {
  cursor: url(/static/media/grab-open.0d468691.png) 10 0, auto !important; }

/* for grabbing stuff */
/* this rule is for when you specifically request this cursor class */
.rpgui-cursor-grab-close,
.rpgui-cursor-grab-close * {
  cursor: url(/static/media/grab-close.aa4a5607.png) 10 0, auto !important; }

/**
* Customized dropdown with rpgui design.
*/
/* dropdown box implemented with list (see rpgui-dropdown.js for details) */
/* note! this class rule affect both the dropdown header and the list elements! */
.rpgui-dropdown-imp,
.rpgui-dropdown {
  /* font */
  text-shadow: -2px 0 black, 0 2px black, 2px 0 black, 0 -2px black;
  color: white;
  /* default size */
  min-height: 40px;
  margin-top: 0px;
  /* border */
  border-style: solid;
  border-width: 7px 7px 7px 7px;
  border-image: url(/static/media/select-border-image.6f02dce3.png) 10% repeat repeat;
  /* background */
  background: url(/static/media/select-background-image.c307c70c.png) repeat repeat;
  background-clip: padding-box;
  background-origin: padding-box;
  background-position: center; }

/* dropdown options list */
ul.rpgui-dropdown-imp {
  padding: 0 0 0 0 !important;
  z-index: 100; }

/* note! this affect only the dropdown header */
/* shows the currently selected value from select element */
.rpgui-content .rpgui-dropdown-imp-header {
  color: white !important;
