/*
* progress bar styling
*/
/* progress bar container */
.rpgui-progress {
  height: 42px;
  width: 100%;
  margin-top: 5px;
  margin-bottom: 5px;
  position: relative; }

/* progress bar left edge */
.rpgui-progress-left-edge {
  position: absolute;
  height: 42px;
  width: 40px;
  left: 0px;
  background-image: url(/static/media/progress-bar-left.6637b30f.png);
  background-size: 100% 100%; }

/* progress bar right edge */
.rpgui-progress-right-edge {
  position: absolute;
  height: 42px;
  width: 40px;
  right: 0px;
  background-image: url(/static/media/progress-bar-right.0d9981df.png);
  background-size: 100% 100%; }

/* progress bar background track */
.rpgui-progress-track {
  position: absolute;
  height: 42px;
  left: 40px;
  right: 40px;
  background-image: url(/static/media/progress-bar-track.9354982b.png);
  background-repeat: repeat-x;
  background-size: 36px 100%; }

/* progress bar - the fill itself */
.rpgui-progress-fill {
  position: absolute;
  top: 9px;
  bottom: 8px;
  left: 0;
  width: 100%;
  background-image: url(/static/media/progress.f1a68a64.png);
  background-repeat: repeat-x;
